.bookingPage .container {
  display: flex;
  gap: 50px;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: normal;
  padding: 40px;
  text-align: center;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("../../imgs/galary/kitchen2.png") center/cover no-repeat;
}
@media (max-width: 768px) {
  .bookingPage .container {
    flex-direction: column;
  }
}
.bookingPage .container form {
  width: 100%;
  max-width: 600px;
  margin: 50px auto;
  background-color: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.bookingPage .container form h2 {
  margin-bottom: 30px;
  color: #bf0a30;
}
.bookingPage .container form .inputContainer {
  display: flex;
  flex-direction: column;
  text-align: right;
  margin-bottom: 20px;
}
.bookingPage .container form .inputContainer label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #000;
}
.bookingPage .container form .inputContainer input,
.bookingPage .container form .inputContainer select,
.bookingPage .container form .inputContainer textarea {
  padding: 10px;
  border: 1px solid white;
  border-radius: 5px;
  font-family: inherit;
  font-size: 1rem;
}
.bookingPage .container form h3 {
  color: #bf0a30;
  margin-bottom: 20px;
}
.bookingPage .container .map-container {
  width: 100%;
  max-width: 900px;
  height: 600px;
  margin: 0 auto;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}
.bookingPage .container .map-container iframe {
  width: 100%;
  height: 100%;
  border: none;
}